import React from "react"
import { Link, StaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

import "./../styles/front-graphic.scss"

export default class FrontGraphic extends React.Component {
    constructor(props) {
        super(props)
        this.state = {expanded: -1}
    }
    render() {
        return (

<StaticQuery
    query={graphql`
        query {
            allFile(filter: {relativeDirectory: {eq: "img/front"}}) {
                edges {
                    node {
                        relativePath
                        childImageSharp {
                            fluid(maxWidth: 3000) {
                                ...GatsbyImageSharpFluid
                            }
                        }
                    }
                }
            }
        }
    `}
    render={data => {
        const front_images = [
            'DSC_0143 (2)(1).jpg',
            'DSC_0016.jpg',
            '_MG_6452.jpg',
            'thumb__MG_5033_1024.jpg',
        ]
        const front_captions = [
            'Lilac Breasted Roller',
            'Livingstone Island',
            'Japanese Cherry Blossoms',
            'Altiplano Sunset',
        ]
        const front_locations = [
            'Kasane Forest Reserve, Botswana',
            'Victoria Falls, Zambia',
            'Baltimore, MD, United States',
            'Salar de Uyuni, Bolivia',
        ]
        const front_links = [
            'botswana',
            'zambia',
            'united-states/baltimore',
            'bolivia',
        ]
        const image_objects = front_images.map(imgFile => data.allFile.edges.find(({ node }) => node.relativePath === 'img/front/' + imgFile))
        return (
            <div id="front-graphic-wrapper">
            {
                image_objects.map((object, index) =>
                    <div key={index}
                        onClick={() => {
                            if (this.state.expanded === index)
                                this.setState({expanded: -1})
                            else
                                this.setState({expanded: index})}
                        }
                        className={this.state.expanded === index ? 'front-image hover' : 'front-image'}
                    >
                        <Img
                            fluid={object.node.childImageSharp.fluid}
                            style={{width: '100%', height: '100%'}}
                        />
                        <div className="caption">
                            <span className="right">
                                {front_captions[index]}<br/>
                                <Link to={"/places/" + front_links[index]} className="underline">
                                    {front_locations[index]}
                                </Link>
                            </span>
                        </div>
                    </div>
                )
            }
            </div>
       )
    }}
/>

           )
    }
}

